<template>
    <div>
        <MyHeader  title=''></MyHeader>
        <group label-width="4em" label-margin-right="2em" label-align="left">
          <x-input title="" placeholder="" ref="input01" v-model='name' required :min="2" :max='10'></x-input>
          <x-input title="&nbsp&nbsp&nbsp" ref="input02" placeholder="" required keyboard="number" is-type="china-mobile" v-model='tel'></x-input>
          <x-input title="&nbsp&nbsp&nbsp" ref="input03"  placeholder="" v-model='address' :min="2" :max='128' required></x-input>
        </group>
        <group style='padding:0 1rem'>
        <mt-button type="danger" size='large' @click='save' ></mt-button>

        </group>
    </div>
</template>
<script>
	import MyHeader from './Header.vue'
	import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'

	import {Toast,MessageBox} from 'mint-ui';
	export default{
		components:{
			MyHeader,
            Group,
            XInput
		},
		data(){
			return{
                name:'',
                tel:'',
                address:''
			}
		},
		methods:{
			save(){
                let uid=this.getCookie('BOOK_user_uid');
                let _this=this;
                if(!this.$refs.input01.valid){
                    Toast({
                          message: '',
                          duration:2000
                        });
                }else if(!this.$refs.input02.valid){
                    Toast({
                          message: '',
                          duration:2000
                        });
                }else if(!this.$refs.input03.valid){
                    Toast({
                          message: '',
                          duration:2000
                        });
                }
                if(this.$refs.input01.valid&&this.$refs.input02.valid&&this.$refs.input03.valid){
                    //console.log(this.name,this.tel,this.address);
                    this.$http.get('updateAddress.php?uid='+uid+'&name='+this.name+'&phone='+this.tel+'&address='+this.address).then(function(res){
                        _this.$router.back();
                    }).catch(function(err){
                        console.log(err);
                    })
                }

            },
            
		},
		computed:{
			
		},
        created(){
            //console.log(this.$route.params.aid)
            let aid=this.$route.params.aid
            let _this=this;
            this.$http.get('getAddressDetail.php?aid='+aid).then(function(res){
                        _this.name=res.data.name;
                        _this.tel=res.data.phone;
                        _this.address=res.data.address
                    }).catch(function(err){
                        console.log(err);
                    })
        }


	}
</script>

<style scoped>

</style>


