<template>
	<div class='person-center'>
		<MyHeader title=''></MyHeader>
		<group>
      		<cell title="" is-link @click.native='updateNickname'>
       	 		  <span slot="value" style="color: #3a3a3a;font-size:14px" >{{username}}</span>
      		</cell>
      		<cell title="" is-link @click.native='developing'>
       	 		  
      		</cell>
      		<cell title="" is-link @click.native='developing'>
       	 		  <span slot="value" style="color: #3a3a3a;font-size:14px">{{phone}}</span>
      		</cell>
      		<cell title="" is-link link="/main/personCenter/myAccount/address">
      		 <span slot="value" style="color: #3a3a3a"></span>
      		</cell>
    	</group>
    	<group style='padding:0 1rem'>
    	<mt-button type="danger" size='large' class='logoutBtn' @click='logout'></mt-button>

    	</group>
	</div>
</template>
<script>
	import MyHeader from './Header.vue'
	import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

	import {Toast,MessageBox} from 'mint-ui'
	export default {
		components:{
			MyHeader,
			Cell,
			Group,
			XButton
		},
		data(){
			return {
				isLogin:false,
				username:'',
				phone:''
			}
		},
		methods:{
			init(){
				let _this=this;
                if(this.getCookie('BOOK_user_uid')!==null){
                    this.isLogin=true;
                    let phone=this.getCookie('BOOK_user_phone');
                    this.$http.get('person_detail.php?phone='+phone).then(function(res){
                    	if(res.status==200){
                    		_this.phone=res.data.phone;
                    		if(res.data.nickname!=='NULL'){
                    			_this.username=res.data.nickname;
                    		}else{
                    			_this.username='';
                    		}
                    	}
                    }).catch(function(err){
                    	console.log(err);
                    })
                }else{
                	this.isLogin=false;
                	this.$router.push('/main/personCenter');
                }
			},
			updateDetail(){
				if(this.isLogin==false){
					this.$router.push('/login');
				}else{
					console.log('')
				}
			},
			logout(){
				MessageBox.confirm('?').then(action => {
                  	this.delCookie('BOOK_user_phone')
                  	this.delCookie('BOOK_user_uid');
                  	this.$router.push('/main/personCenter');
                }).catch(function(res){
                    //console.log(res)
                });
			},
			updateNickname(){
				var nickname=this.username;
				var _this=this;
				MessageBox.prompt('').then(({ value, action }) => {
					this.$http.get('update_nickname.php?phone='+this.phone+'&nickname='+value).then(function(res){
						if(res.data.msg=='succ'){
							Toast({
					            message: '',
					            duration: 1000
					          });
							_this.init();
						}						
					}).catch(function(err){
						console.log(err);
					})
                 
                }).catch(function(res){
                    //console.log(res)
                });
			},
			developing(){
				Toast({
					message: '',
					duration: 1000
				});
			}
		},
		created(){
			this.init();
		}

	}

</script>
<style scoped>

</style>


